;\r\n        justify-content: center;\r\n        flex-direction: row;\r\n        font-family: \"Oswald\", sans-serif;\r\n\r\n        .headline {\r\n            color: #444; // Darker gray for headline\r\n            display: flex;\r\n            flex-direction: column;\r\n            align-items: center;\r\n            justify-content: center;\r\n            width: 50vw;\r\n            height: 100vh;\r\n            background: #e0e0e0; // Light gray background for headline section\r\n        }\r\n\r\n        .interaction {\r\n            height: 100vh;\r\n            width: 50vw;\r\n            background: #fff; // White background for interaction section\r\n\r\n            .form {\r\n                height: 100vh;\r\n                width: 50vw;\r\n                display: flex;\r\n                flex-direction: column;\r\n                align-items: center;\r\n                justify-content: center;\r\n\r\n                #zip {\r\n                    margin-bottom: 1rem;\r\n                }\r\n\r\n                label {\r\n                    display: block;\r\n                    font-size: 1.25rem; // Adjusted font size\r\n                    color: #555; // Slightly lighter gray\r\n                }\r\n\r\n                input {\r\n                    display: block;\r\n                    height: 3rem;\r\n                    width: 25rem;\r\n                    background: #ffffff;\r\n                    color: #333; // Dark gray text color\r\n                    border: 2px solid #ccc; // Light gray border\r\n                    font-size: 1rem; // Adjusted font size\r\n                    font-family: \"Oswald\", sans-serif;\r\n                    border-radius: 8px;\r\n                    padding: 0.5rem;\r\n\r\n                    &::placeholder {\r\n                        color: #999; // Light gray placeholder text\r\n                    }\r\n                }\r\n\r\n                button {\r\n                    margin-top: 1rem;\r\n                    width: 10rem; // Slightly wider button\r\n                    height: 3rem;\r\n                    background: #007bff; // Blue background\r\n                    color: white;\r\n                    font-size: 1rem; // Adjusted font size\r\n                    font-family: \"Oswald\", sans-serif;\r\n                    border: none;\r\n                    box-shadow: 2px 4px 5px rgba(0, 0, 0, 0.1); // Subtle shadow\r\n                    border-radius: 8px;\r\n                    cursor: pointer; // Pointer cursor on hover\r\n                    transition: background 0.3s ease; // Smooth transition\r\n\r\n                    &:hover {\r\n                        background: #0056b3; // Darker blue on hover\r\n                    }\r\n                }\r\n\r\n                #entryHolder {\r\n                    padding: 1rem;\r\n                    height: 50vh;\r\n                    background: #f9f9f9; // Light gray background for entry holder\r\n                    border-radius: 8px;\r\n                    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); // Subtle shadow\r\n\r\n                    p {\r\n                        display: flex;\r\n                        flex-direction: row;\r\n                        align-items: center;\r\n                        font-size: 1.25rem; // Adjusted font size\r\n                        margin: 0;\r\n                        color: #555; // Slightly lighter gray\r\n                    }\r\n\r\n                    #city-image {\r\n                        width: 50%;\r\n                        border-radius: 8px; // Rounded corners for image\r\n                    }\r\n\r\n                    #content {\r\n                        margin-top: 1rem;\r\n                        margin-bottom: 2rem;\r\n                        display: flex;\r\n                        flex-direction: row;\r\n                        justify-content: space-around;\r\n                        align-items: stretch;\r\n\r\n                        